<template>
  <div class="reg">
    <form class="reg-wrapper">
      <div class="reg-wrapper-logo">
        <img ondragstart="return false" src="../assets/auth/logo-icon.png" alt="">
        <p class="reg-wrapper-logo-title">Придумайте пароль</p>
      </div>
      <div class="reg-wrapper-row">
        <input ref="form_4" v-model="password" placeholder="Пароль..." type="text" class="reg-wrapper-row-input input-2">
      </div>
      <div class="reg-wrapper-row">
        <input v-model="password_verify" placeholder="Подтвердите пароль..." type="text" class="reg-wrapper-row-input input-2">
      </div>
      <div class="reg-wrapper-row">
        <button type="submit" @click="access" :disabled="isDisabled" class="login-btn">Завершить регистрацию</button>
      </div>
      <div class="reg-wrapper-row cancel">
        <img @click="$router.push(`/auth`)" ondragstart="return false" src="../assets/auth/back.png" alt="">
        <p @click="$router.push(`/auth`)" class="cancel-text">Отменить и вернуться ко входу</p>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'registration_verification',
  data: () => ({
    password: '',
    password_verify: '',
    isActiveBtn: false
  }),
  computed: {
    isDisabled() {
      if(this.password !== '' && this.password === this.password_verify && !this.isActiveBtn) {
        return false
      } else {
        return true
      }
    },
    userData() {
      return this.$store.getters['registration/userData']
    },
    token () {
      return this.$store.getters['auth/token']
    },
  },
  created() {
    if(!localStorage.getItem('userphone') && !localStorage.getItem('username') && !localStorage.getItem('potential_token')) {
      this.$router.push('/dashboard')
    }
    setTimeout(x => {
      this.$nextTick(() => this.setFocus());
    }, 1);
  },
  methods: {
    setFocus: function() {
      this.$refs.form_4.focus()
    },
    access () {
      if(this.password === this.password_verify) {
        this.$store.dispatch('common/SET_LOADED', 'loading')
        this.isActiveBtn = true
        axios.put("https://cab.dev1.автоинлайн.рф/api/students/me/", {
          first_name: this.userData.name  || localStorage.getItem('username'),
          password: this.password,
          password_confirm: this.password_verify
        },
        { 
        headers: {
          'Authorization': 'Bearer ' + JSON.parse(localStorage.getItem('potential_token')).access,
          'Content-Type': 'application/JSON'
        }
        }).then(data => {
          axios.post("https://cab.dev1.автоинлайн.рф/api/token/create/", {
            "phone": this.userData.phone || localStorage.getItem('userphone'),
            "password": this.password
          }).then(payload => {
            localStorage.setItem('token', JSON.stringify(payload.data))
            this.$store.dispatch('auth/SET_TOKEN', payload.data)
            this.$router.push('/dashboard')
            localStorage.setItem('userphone', '')
            localStorage.setItem('username', '')
            localStorage.setItem('potential_token', '')
            this.isActiveBtn = false
          })
          
        }).catch(err => {
          this.isActiveBtn = false
          this.$store.dispatch('common/SET_LOADED', true)
          alert('Возникла ошибка')
        })
      } else {
        alert('Пароли различаются')
      }
    }
  }
}
</script>
 
<style lang="scss" scoped>
  .reg {
    &-wrapper {
      min-height: 100vh;
      height: 100%;
      margin: 0px auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      user-select: none;
      width: 320px;
      &-logo {
        margin: 20px auto;
        margin-top: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        user-select: none;
        & img {
          margin: 0px auto;
        }
        &-title {
          font-family: 'Vox-Mit';
          font-size: 1.6rem;
          color: #fff;
          margin: 10px 0px 0px 0px;
        }
      }
      &-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 12px -4px;
        user-select: none;
        &-input {
          background: transparent;
          border: 1px solid #7b7c8f;
          height: 48px;
          display: flex;
          align-items: center;
          padding: 15px 16px;
          margin: 0px 4px;
          border-radius: 24px;
          user-select: none;
          &.input-1 {
            cursor: pointer;
            & span {
              color: #dddfeb;
              font-family: 'SSans-Reg';
              font-size: 1.2rem;
              display: flex;
              align-items: center;
            }
            & img {
              margin-left: 24px;
              margin-top: 3px;
            }
          }
          &.input-2 {
            color: #dddfeb;
            font-family: 'SSans-Reg';
            font-size: 1.2rem;
            width: 100%;
            &::placeholder {
              color: #dddfeb;
              font-family: 'SSans-Reg';
              font-size: 1.2rem;
            }
          }
        }
        &-text {
          margin: 0px 0px;
        }
        & .login-btn {
          width: 100%;
          color: #fff;
          font-size: 1.2rem;
          font-family: 'SSans-Reg';
          height: 48px;
          background: linear-gradient(to bottom, rgb(255, 111, 47), #f64628);
          border-radius: 24px;
          border: none;
          cursor: pointer;
          outline: none;
          &:disabled {
            background: rgba(127, 127, 127, .8);
            color: rgba(255, 255, 255, .8);
          }
        }
        &.cancel {
          display: flex;
          align-items: center;
          justify-content: center;
          margin-top: auto;
          & img {
            margin-right: 10px;
            cursor: pointer;
          }
          & .cancel-text {
            color: #f44b2f;
            font-family: 'SSans-Reg';
            font-size: 1rem;
            cursor: pointer;
          }
        }
      }
    }
  }
</style>
